.container {
    background-color: #ffffff71;
    width: 350px;
    height: auto;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 5;
    gap: 0;
    color: #120907;
}

.button {
    background-color: white;
    border: none;
    font-size: 1rem;
    font-weight: bold;
    position: absolute;
    top: 5px;
    right: 5px;
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
    height: 20px;
    width: 20px;
    cursor: pointer;
}

.button:hover {
    background-color: black;
    color: white;
}

.iframe {
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0;
}

.name {
    font-size: 2rem;
    font-weight: 500;
    color: #120907;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    padding: 0;
    margin: 0;
}

.temps {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

.min,
.max {
    width: 100%;
    display: flex;
}

.label {
    font-size: 1rem;
    font-weight: bold;
}

.top {
    display: flex;
    gap: 10px;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.img {
    width: 50%;
    height: auto;
    justify-self: center;
}

.info {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.desc {
    font-size: 1rem;
}